@import url('https://fonts.googleapis.com/css2?family=Bungee+Spice&family=Akaya+Kanadaka&display=swap');
*
{
    padding: 0;
    margin: 0;
}
body{
    background-color: #000000;
    height: 100vh;
}
.title-container{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 25px;
}
.title-container h1{
    color: #FFFFFF;
    margin-bottom: 20px;
    font-size: 45px;
    text-shadow: 2px 2px 4px #5E5D5D;
    font-family:"Akaya Kanadaka", serif;
  font-weight: 400;
  font-style: normal;
}
.title-container input{
    width: 25%;
    padding: 15px;
    border-radius: 10px;
    margin-bottom: 10px;
    font-size: 20px;
 border: none;
}
.title-container input::placeholder{
    font-family: "Keania One", sans-serif;
  font-weight: 400;
  font-style: normal;
    color: #000000;
}
.title-container button{
    padding: 10px 20px;
    font-size: 18px;
    border: none;
    border-radius: 10px;
    box-shadow: rgba(45, 35, 66, 0.4) 0 2px 4px,rgba(45, 35, 66, 0.3) 0 7px 13px -3px,#89898C 0 -3px 0 inset;
    box-sizing: border-box;
    color: #000000;
    cursor: pointer;
    margin-top: 10px;
    font-family: "Keania One", sans-serif;
  font-weight: 400;
  font-style: normal;
}
.title-container button:hover{
    box-shadow: rgba(45, 35, 66, 0.4) 0 4px 8px, rgba(45, 35, 66, 0.3) 0 7px 13px -3px, #B2B2B8 0 -3px 0 inset;
    transform: translateY(-2px);
}
nav{
    background-color: #FFFFFF;
    padding: 20px 0;
    display: flex;
    justify-content: center;
    margin-top: 15px;
    font-family: "Keania One", sans-serif;
  font-weight: 400;
  font-style: normal;
  box-shadow: 0 4px 8px 0 rgba(255, 217, 0, 0.379), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
nav ul{
    display: flex;
    list-style-type: none;
    justify-content: space-between;
    align-items: center;
    width: 40%;
    color: #000000;
    font-weight: bold;
    transition: all 0.5s ease-in-out;
}
nav ul li{
    cursor: pointer;
    font-size: 22px;
}
nav ul li:hover{
   transform: scale(1.4);
}
.nav-active{
    color: rgb(153, 90, 18);
    border: 2px solid black;
    padding: 12px;
    border-radius: 18px;
    box-shadow: rgba(45, 35, 66, 0.4) 0 4px 8px, rgba(45, 35, 66, 0.3) 0 7px 13px -3px, #B2B2B8 0 -3px 0 inset;
    transform: translateY(-2px);
}
.answer-container{
    padding: 20px;
    color: #FFFFFF;
    font-family: "Keania One", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 35px;
  background-color: #000000;
  height: 100vh;
}